
import { Options, Vue } from 'vue-class-component';
import { Category } from '../App.vue';

@Options({
  props: {
    category: {
      type: Object,
      required: true
    },
    showChildrens: {
      type: Boolean,
      required: true
    },
    checkedCategories: {
      type: Array,
      required: true
    },
    depth: {
      type: Number,
      required: true
    }
  },
  watch: {
    showChildrens(value: boolean) {
      if (!value) {
        this.hoveredCategory = ''
      }
    }
  },
  emits: [
    'select',
  ],
  data() {
    return {
      hoveredCategory: ''
    }
  },
  methods: {
    handleSelect(category: Category): void {
      this.$emit('select', category)
    }
  }
})
export default class BaseCategoryFilterItem extends Vue {
  category!: Category
  checkedCategories!: Array<string>
  depth!: number
  hoveredCategory!: string
  showChildrens!: boolean
  handleSelect!: ((category: Category) => void)
}
